@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 20px;
  background-color: #202124;
  color: bisque;
}

.hw_title {
  text-align: center;
  margin: 50px 0;
  font-family: 'Times New Roman', Times, serif;
}
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.wrap_app {
  max-width: 350px;
  background-color: black;
  margin: 0 auto;
  border: solid 1px bisque;
  border-radius: 5px;
  padding: 20px;
}

.wrap_timer_list {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

.wrap_timer {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 15px;
  gap: 15px;
  padding: .937rem;
  border: 3px solid #202124;
  border-radius: 5px;
}

.btn_timer {
  background-color: bisque;
  border: none;
  width: 6.675rem;
  padding: .937rem;
  border: 1px solid #242424;
  border-radius: 3px;
  transition: 0.1s;
}

.btn_timer:hover {
  background-color: #242424;
  color: bisque;
  border: 1px solid bisque;
}

.btn_timer:active {
  -webkit-transform: translateY(8px);
          transform: translateY(8px);
}

.wrap_progress {
  width: 100%;
}

.progress {
  height: 5px;
  background-color: bisque;
}
